@import '../../../../asset/style/variable';

.selectBtn {
  border: none;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
  color: $snow;
  background: transparent;
  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none;
    border: none;
    color: $snow;
    background: transparent;
    box-shadow: none;
    outline: none;
    .iconDropdown {
      color: $snow;
    }
  }

  &:disabled {
    color: $stone;
    cursor: not-allowed;
    &:hover {
      .iconDropdown {
        color: $stone;
      }
    }
  }
}

.logo {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  border-radius: $border-radius;
}

.symbol {
  margin-right: 4px;
}

.iconDropdown {
  color: $stone;
  &::before {
    font-weight: $font-weight-xl !important;
  }
}

.loader {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-left: 4px;
  border: 3px solid $snow;
  border-top: 3px solid $blue;
  border-radius: 50%;
  vertical-align: -3px;
  animation: spin 0.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
