@import '../../../asset/style/variable';

.wrap {
}

.wrapBtnSellect {
  display: inline-block;
  min-width: 120px;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 6px;
}

.balance {
  max-width: 160px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.button {
  width: 30px;
  height: 30px;
  border: none;
  font-size: $font-size-xl;
  background: transparent;
  color: $stone;
  &:hover {
    color: $snow;
  }
}

.check {
  color: $green;
}
