@import '../../../asset/style/variable';

.title {
  height: 16px;
  margin-bottom: 24px;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
  line-height: $font-height-default;
  text-align: center;
  color: $snow;
}

.pairInfoWrap {
  margin-bottom: 16px;
}

.pairInfoItem {
  margin: 0 -6px 12px -6px;
  margin-left: -6px;
  border: $border-blur;
  border-radius: $border-radius;
  font-size: $font-size-sm;
  font-weight: $font-weight-md;
  text-align: center;
  color: $stone;
}

.pairInfoValue {
  height: 21px;
  font-size: $font-size-md;
  font-weight: $font-weight-xl;
  color: $snow;
}

.tokenSecurityWrap {
  margin: 0 -6px 0px -6px;
  border: $border-blur;
  border-radius: $border-radius;
}

.tokenSecurityItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 34px;
  border-bottom: $border-blur;
  padding: 6px 12px;
  font-size: $font-size-md;
  font-weight: $font-weight-md;
  color: $stone;
  &:last-child {
    border: none;
  }
}

.tokenSecuritValue {
  font-weight: $font-weight-xl;
  color: $snow;
}